.Home {
    text-align: center;
    background-color: #282c34;
}

ul {
    list-style-type: none;
    position: fixed;
    top: 0;
    width: 100%;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #282c34;
    align-items: center;
}

li {
    float: left;
}

.Home-header {
    align-items: center;
    background-color: #282c34;
    color: rgb(209, 209, 209);
    display: flex;
    flex-direction: column;
    font-size: calc(20px + 2vmin);
    font-weight: bold;
    justify-content: center;
    min-height: 50vh;
}